.blog-container {
  min-height: calc(100vh - 34px);
  display: flex;
  padding-top: 2vh;
  margin-bottom: 30px;
}

.blog-container .loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.blog-container .loader svg {
  width: 200px;
  height: 200px;
}

.blog-container .utilities {
  width: 20%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.blog-container .utilities .account-alert {
  font-size: 1.2rem;
  font-weight: 500;
  margin-left: 2rem;
  text-align: center;
}

.blog-container .utilities .utility-item {
  margin-bottom: 2rem;
  font-size: 1.6rem;
  font-weight: 700;
  cursor: pointer;
}

.blog-container .utilities .like:hover {
  color: #f33838;
}

.blog-container .utilities .liked {
  color: #f33838;
}

.blog-container .utilities .comment:hover {
  color: #368dd6;
}

.blog-container .blog {
  align-self: center;
  width: 85%;
}

.blog-container .blog .card {
  width: 80%;
  border-radius: 12px;
  padding: 2rem 3rem;
}

.blog-container .blog .card .card-body {
  padding: 0.5rem;
}

.blog-container .blog .card .card-body h1 {
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 1rem;
  margin-top: 0.5rem;
}

.comment-box form button {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #368dd6;
  color: #fff;
  border-radius: 7px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  margin-bottom: 2rem;
}

.comment-box form button:hover {
  background-color: #257cc5;
}

.comment-container .comment {
  padding: 1rem 1rem;
  border-radius: 7px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.19);
}

.comment-container .comment .comment-author {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.2rem;
}

.comment-container .comment .comment-date {
  font-size: 0.8rem;
  font-weight: 400;
  color: #181717;
  margin-bottom: 0.5rem;
}

.comment-container .comment .comment-content {
  font-size: 1.2rem;
  font-weight: 500;
  color: #181717;
}

.copy-link .copy-link-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.copy-link .copy-link-container .url {
  font-size: 1rem;
  font-weight: 500;
  color: #d64531;
}

.copy-link .copy-link-container .copy {
  cursor: pointer;
  transition: 0.3s;
}

.copy-link .copy-link-container .copy:hover {
  color: #f33838;
  transform: scale(1.1);
}

.copy-link .copied-text {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 0.7rem;
  font-weight: 500;
}

.copy-link .copied-text span {
  border-radius: 7px;
  padding: 0.5rem 1rem;
  /* background-color: #d4b9e4; */
}
