.blog-container {
  min-height: calc(100vh - 34px);
  display: flex;
  padding-top: 2vh;
  margin-bottom: 30px;
}

.blog-container .loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.blog-container .loader svg {
  width: 200px;
  height: 200px;
}

.blog-container .utilities {
  width: 20%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.blog-container .utilities .account-alert {
  font-size: 1.2rem;
  font-weight: 500;
  margin-left: 2rem;
  text-align: center;
}

.blog-container .utilities .utility-item {
  margin-bottom: 2rem;
  font-size: 1.6rem;
  font-weight: 700;
  cursor: pointer;
}

.blog-container .utilities .like:hover {
  color: #f33838;
}

.blog-container .utilities .liked {
  color: #f33838;
}

.blog-container .utilities .comment:hover {
  color: #368dd6;
}

.blog-container .blog {
  align-self: center;
  width: 85%;
}

.blog-container .blog .card {
  width: 80%;
  border-radius: 12px;
  padding: 2rem 3rem;
}

.blog-container .blog .card .card-body {
  padding: 0.5rem;
}

.blog-container .blog .card .card-body h1 {
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 1rem;
  margin-top: 0.5rem;
}

.comment-box form button {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #368dd6;
  color: #fff;
  border-radius: 7px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  margin-bottom: 2rem;
}

.comment-box form button:hover {
  background-color: #257cc5;
}

.comment-container .comment {
  padding: 1rem 1rem;
  border-radius: 7px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.19);
}

.comment-container .comment .comment-author {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.2rem;
}

.comment-container .comment .comment-date {
  font-size: 0.8rem;
  font-weight: 400;
  color: #181717;
  margin-bottom: 0.5rem;
}

.comment-container .comment .comment-content {
  font-size: 1.2rem;
  font-weight: 500;
  color: #181717;
}

.copy-link .copy-link-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.copy-link .copy-link-container .url {
  font-size: 1rem;
  font-weight: 500;
  color: #d64531;
}

.copy-link .copy-link-container .copy {
  cursor: pointer;
  transition: 0.3s;
}

.copy-link .copy-link-container .copy:hover {
  color: #f33838;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.copy-link .copied-text {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 0.7rem;
  font-weight: 500;
}

.copy-link .copied-text span {
  border-radius: 7px;
  padding: 0.5rem 1rem;
  /* background-color: #d4b9e4; */
}

.footer {
  background-color: #e3d0ee;
  padding: 20px;
  text-align: center;
  font-size: 14px;
  color: #363535;
  position: relative;
  left: 0;
  bottom: 0;
  width: 100%;
}

.footer h3 {
  font-size: 1.5rem;
  font-weight: bold;
  color: #368dd6;
}

* {
  font-family: "Ubuntu", sans-serif;
}

.navbar {
  margin-bottom: 1vh;
  background-color: #fff;
  border-bottom: 1px solid #4443;
}

.navbar .container .navbar-brand {
  cursor: pointer;
  color: #368dd6;
  font-size: 1.8rem;
  font-style: italic;
  font-weight: bolder;
}

.navbar .container .navbar-collapse form input {
  width: 18vw;
  color: #333;
  margin-left: 2vw;
  border: 1px solid #368dd6;
  background-color: #ffffff;
}

.navbar .container .navbar-collapse form input:focus {
  outline: none;
  box-shadow: none;
}

.navbar .container .navbar-collapse form button {
  border: none;
  outline: none;
  background-color: #368dd6;
}

.navbar .container .navbar-collapse form button:hover {
  background-color: #146bb4;
}

.navbar .container .navbar-collapse .new-post {
  width: 100%;
  border: none;
  margin-right: 1vw;
  border-radius: 4px;
  background-color: #368dd6;
}

.navbar .container .navbar-collapse .new-post:hover {
  background-color: #146bb4;
}

.navbar .container .navbar-collapse .user-icon {
  border-radius: 50%;
  background-color: #2e2d2e;
  border: 2px solid #a6a5a7;
  transition: all 0.3s ease-in-out;
}

.navbar .container .navbar-collapse .user-icon:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.navbar .container .navbar-collapse .dropdown-toggle::after {
  display: none;
}

.navbar .container .navbar-collapse .login {
  width: 100%;
  border: none;
  margin-right: 1vw;
  border-radius: 4px;
  background-color: #368dd6;
}

.navbar .container .navbar-collapse .login:hover {
  background-color: #146bb4;
}

@media only screen and (max-width: 768px) {
  .navbar .container .navbar-collapse .login {
    margin-top: 1rem;
  }
}

.createpost-container {
  min-height: calc(100vh - 34px);
}

.createpost-container .container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 8vh;
  padding-bottom: 12vh;
}

.createpost-container .container .card {
  /* border: none; */
  width: 70%;
  padding: 1rem;
  border-radius: 10px;
}

.createpost-container .container .card .heading {
  font-size: 2.2rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

/* input type file */
.createpost-container .container .card form label {
  font-size: 1.3rem;
  font-weight: 700;
}

.createpost-container .container .card form .img-preview {
  width: 60%;
  height: 30vh;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.createpost-container .container .card form .img-preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.createpost-container .container .card form .remove-image {
  background: #f44336;
  color: #fff;
  border-radius: 7px;
  cursor: pointer;
  font-size: medium;
  font-weight: bold;
  width: 10vw;
}

.createpost-container .container .card form .remove-image:hover {
  background: #c9291e;
}

.createpost-container .container .card form input[type="file"] {
  width: 30%;
  padding: 0.5rem;
  border-radius: 10px;
  border: 1px solid #ccc;
  margin-bottom: 1rem;
}

.createpost-container .container .card form input[type="text"] {
  width: 80%;
  color: #000;
  font-size: 2rem;
  font-weight: bolder;
  border: none;
  margin-bottom: 3rem;
}

.createpost-container .container .card form input[type="text"]:focus {
  outline: none;
  box-shadow: none;
}

.createpost-container .container .card form input[type="text"]::-webkit-input-placeholder {
  color: #000;
  font-size: 2rem;
  font-weight: bolder;
}

.createpost-container .container .card form input[type="text"]:-ms-input-placeholder {
  color: #000;
  font-size: 2rem;
  font-weight: bolder;
}

.createpost-container .container .card form input[type="text"]::placeholder {
  color: #000;
  font-size: 2rem;
  font-weight: bolder;
}

.createpost-container .container .card form .ck-editor__editable {
  min-height: 25vh;
}

.createpost-container .container .card form .submit {
  width: 9rem;
  border-radius: 5px;
  border: none;
  padding: 0.3rem;
  background-color: #368dd6;
  font-size: 1.3rem;
  font-weight: 500;
}

.createpost-container .container .card .error {
  color: #f44336;
  text-align: center;
  font-size: 1.2rem;
  font-weight: bold;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

